import React from 'react';
import { SubscribeRow, SubscribeInput, SubmitButton, validateEmail, MenuItem }
  from './helpers/contact';

class SubscribeContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      validated: false,
      subscribeButtonVisible: true
    };
  }

  updateState(email) {
    if (validateEmail(email).emailValid) {
      this.setState({ email, validated: true });
    } else {
      this.setState({ email });
    }
  }

  render() {
    const { validated, subscribeButtonVisible } = this.state;

    if (subscribeButtonVisible) {
      return (
        <MenuItem
          onClick={() => this.setState({
            subscribeButtonVisible: !subscribeButtonVisible
          })}
        >
          subscribe
        </MenuItem>
      );
    } else if (!subscribeButtonVisible && validated) {
      return (
        <SubscribeRow>
          <SubscribeInput
            type="text"
            placeholder="enter your email"
            onChange={(event) => { this.updateState(event.target.value); }}
            validated={validated}
            autocomplete="off"
          />
          <SubmitButton
            name="check-circle"
            style={{ fontSize: 22, color: 'black', marginLeft: 10, marginRight: 10 }}
          />
        </SubscribeRow>
      );
    }
    return (<SubscribeRow>
      <SubscribeInput
        type="text"
        placeholder="enter your email"
        onChange={(event) => { this.updateState(event.target.value); }}
        validated={validated}
        autocomplete="off"
      />
    </SubscribeRow>);
  }
}

export default SubscribeContainer;



